import EvanCheckbox from '@/components/evan-checkbox/evan-checkbox.vue'
import EvanCheckboxGroup from '@/components/evan-checkbox/evan-checkbox-group.vue'
import UniIcons from '@/components/uni-icons/uni-icons.vue'
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
import {GetShiping,RemoveShiping,CountTotalPrice,GetDefaultAdd} from "../../common/api.js"
export default {
	components: {
		EvanCheckbox,
		EvanCheckboxGroup,
		UniIcons,
		uniNumberBox,
		
	},
	data() {
		return {
			totalPrice:0,
			allCheck:false,
			carData:[],
			checkList:[],
			settData:[]
		}
	},
	onLoad(){
		this.pageInit();
	},
	methods: {
		pageInit(){
			GetShiping().then(res=>{
				if(res.status){
					let d = [...res.data.data];
					for(let i = 0,len = d.length;i<len;i++){
						d[i].checked = false;
					}
					this.carData = d;
				}
			})
		},
		allChange(){
			let a = this.carData.every((e)=>{
				return e.checked==true;
			});
			if(a){
				this.carData.forEach((e)=>{
					e.checked = false;
				});
				this.tempConduct();
			}else{
				this.carData.forEach((e)=>{
					e.checked = true;
				});
				this.tempConduct();
			}
		},
		remove(item){
			let _this = this;
			uni.showModal({
			    title: '提示',
			    content: '确定要删除吗？',
			    success: function (res) {
			        if (res.confirm) {
						let d = {id:item.id}
			            RemoveShiping(d).then(res=>{
							if(res.status){
								_this.pageInit();
							}
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		tempConduct(){
			const _this = this;
			let arr = _this.carData.filter((e,i) => {
				return e.checked===true;
			});
			let t = [];
			
			arr.forEach((e,i)=>{
				let j = {
					id:e.commodity.id,
					number:e.number
				}
				t.push(j);
				t.length===_this.carData.length?(_this.allCheck = true):(_this.allCheck = false);
			});
			_this.settData = arr;
			
			let d = {productData:t}
			CountTotalPrice(d).then(res=>{
				if(res.status){
					_this.totalPrice = res.totalPrices/100;
				}
			})
		},
		//选中
		checkChange(item){
			this.tempConduct();
		},
		bindChange(item){
			if(item.checked){
				this.tempConduct();
			}
		},
		//去结算
		toSettlement(){
			GetDefaultAdd().then(res=>{
				if(res.status){
					let d = this.settData;
					this.$store.commit("SET_SETARR", d);
					this.$store.commit("SET_DEFAADD",res.data);
					this.$store.commit("SET_PRICE",this.totalPrice)
					uni.navigateTo({
					    url: '../settlement/settlement?pay_type=1'
					});
				}else{
					if(res.code == '204'){
						let d = this.settData;
						this.$store.commit("SET_SETARR", d);
						this.$store.commit("SET_DEFAADD",null);
						this.$store.commit("SET_PRICE",this.totalPrice)
						uni.navigateTo({
						    url: '../settlement/settlement?pay_type=1'
						});
					}
				}
			})
			
		}	
	}
}
